/**
 * App styles
 */

.App-main {
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
}

.App-inputs {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}

/**
 * NetworkViewer styles
 */

.App-network {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.App-network .Connection path {
  stroke: rgba(0, 0, 0, 0.5);
}
.App-network .Connection:hover path {
  stroke: rgba(0, 0, 0, 1);
}

.App-network .Connection text {
  fill: rgba(0, 0, 0, 0.2);
}
.App-network .Connection:hover text {
  fill: rgba(0, 0, 0, 1);
}



